import * as React from "react";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import LinkButtonPublic from "../../Componets/LinkButtonPublic";
import { GitHub, Web, Download } from "@mui/icons-material";
import {
	Divider,
	Avatar,
	List,
	ListItem,
	ListItemText,
} from "@mui/material";

export default function GithubHeaderInner(props) {
	const { lang, list } = props;
	const created_event = new Date(list.created_at);
	const updated_event = new Date(list.updated_at);
	const options = {
		weekday: "short",
		year: "numeric",
		month: "short",
		day: "numeric",
	};
	let createdDate = created_event.toLocaleDateString(lang.type, options);
	let updatedDate = updated_event.toLocaleDateString(lang.type, options);

	return list.owner ? (
		<Paper sx={{ p: 2, margin: 1, flexGrow: 1, borderRadius: 6, boxShadow: 5 }}>
			<Grid container spacing={4}>
				<Grid item>
					<LinkButtonPublic
						color="success"
						icons={true}
						to={list.owner.html_url}
						target="_blank"
						type="badge"
						badgeContent={
							<GitHub sx={{ fontSize: 45, color: "text.primary" }} />
						}
						sx={{
							boxShadow: 3,
							borderRadius: 6,
						}}
					>
						<Avatar
							sx={{ width: 256, height: 256, borderRadius: 5 }}
							alt={list.owner.login}
							title={list.owner.login}
							src={list.owner.avatar_url}
						/>
					</LinkButtonPublic>
				</Grid>
				<Grid item xs={12} sm container>
					<Grid item xs container direction="column" spacing={1}>
						<Grid item xs>
							<Typography
								gutterBottom
								variant="h2"
								fontWeight={700}
								component="h1"
								sx={{ my: 0 }}
							>
								{list.name}
							</Typography>
							<Typography variant="body2" gutterBottom color="text.secondary">
								{list.description} / {list.language}
							</Typography>
							<Divider sx={{ my: 1 }} />
							<Grid container spacing={1} columns={16}>
								<Grid item xs sx={{ p: "auto", textAlign: "center" }}>
									{lang.git.Created}: <b>{createdDate}</b>
								</Grid>
								<Grid item xs sx={{ p: "auto", textAlign: "center" }}>
									{lang.git.Updated}: <b>{updatedDate}</b>
								</Grid>
								<Grid item xs sx={{ p: "auto", textAlign: "center" }}>
									{lang.git.Branch}: <b>{list.default_branch}</b>
								</Grid>
							</Grid>
						</Grid>

						<Grid container spacing={1} columns={16} color="text.secondary">
							<Grid item xs sx={{ p: "auto" }}>
								<LinkButtonPublic
									to={list.html_url}
									target="_blank"
									startIcon={<GitHub />}
									icons={false}
									sx={{ m: "auto" }}
								>
									Repo
								</LinkButtonPublic>
							</Grid>
							<Grid item xs sx={{ p: "auto" }}>
								<LinkButtonPublic
									to={list.url + "/tarball/" + list.default_branch}
									target="_blank"
									startIcon={<Download />}
									icons={false}
									sx={{ m: "auto" }}
								>
									tar.gz
								</LinkButtonPublic>
								<LinkButtonPublic
									to={list.url + "/zipball/" + list.default_branch}
									target="_blank"
									startIcon={<Download />}
									icons={false}
									sx={{ m: "auto" }}
								>
									zip
								</LinkButtonPublic>
							</Grid>
							{list.homepage !== "" ? (
								<Grid item xs>
									<LinkButtonPublic
										to={list.homepage}
										target="_blank"
										startIcon={<Web />}
										icons={false}
										sx={{ width: "100%", m: "auto" }}
									>
										GitHub Pages
									</LinkButtonPublic>
								</Grid>
							) : (
								""
							)}
						</Grid>
					</Grid>
				</Grid>
				<Grid item>
					<List dense>
						{list.topics.map((s, i) => (
							<ListItem key={i} sx={{ m: 0, p: 0 }}>
								<ListItemText primary={s} />
							</ListItem>
						))}
					</List>
				</Grid>
			</Grid>
		</Paper>
	) : (
		<>ddd</>
	);
}
